import { Link } from 'react-router-dom';

const ElectionManagement = () => {
  return (
    <div>
      <h1>Election Management</h1>
      <p>Oversee and create elections for a smooth electoral process.</p>
      <Link to="/view-elections">View Elections</Link>
      <Link to="/create-election">Create New Election</Link>
      <h2>Reports</h2>
      <Link to="/view-reports">View Reports</Link>
      <h2>System Settings</h2>
      <Link to="/system-settings">Update Settings</Link>
    </div>
  );
};

export default ElectionManagement;
