// src/pages/VotingHistory.js
import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate hook
import './VotingHistory.css'; // Import CSS for styling

const VotingHistory = () => {
  const navigate = useNavigate(); // Initialize the navigate function

  // Sample voting history data (replace with real data as needed)
  const votingRecords = [
    { date: '2024-05-10', election: 'Presidential Election', status: 'Voted' },
    { date: '2023-11-07', election: 'Local Council Election', status: 'Voted' },
    { date: '2022-06-15', election: 'State Senate Election', status: 'Did Not Vote' },
  ];

  return (
    <div className="voting-history-container">
      {/* Back button */}
      <button className="back-button" onClick={() => navigate('/citizen')}>Back</button>

      <h2>Your Voting History</h2>
      <p>Details of your past voting records are displayed below:</p>

      <table className="voting-history-table">
        <thead>
          <tr>
            <th>Date</th>
            <th>Election</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          {votingRecords.map((record, index) => (
            <tr key={index}>
              <td>{record.date}</td>
              <td>{record.election}</td>
              <td>{record.status}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <div className="footer">
        <p>If you have questions about your voting history, please contact your local election office.</p>
      </div>
    </div>
  );
};

export default VotingHistory;
