import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Box, Typography, MenuItem } from '@mui/material';
import axios from 'axios';

const API_URL = 'https://electionmonitoringbackend.up.railway.app/api/users';

const AddUser = () => {
  const navigate = useNavigate();
  
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [role, setRole] = useState('');
  const [phone, setPhone] = useState('');
  const [registeredDate, setRegisteredDate] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!name || !email || !role || !phone || !registeredDate) {
      setErrorMessage('All fields are required.');
      return;
    }

    const newUser = { name, email, role, phone, registeredDate };

    try {
      await axios.post(API_URL, newUser);
      setSuccessMessage('User added successfully!');
      setErrorMessage('');
      resetForm();
    } catch (error) {
      setErrorMessage('Failed to add user. Please try again.');
      console.error(error);
    }
  };

  const resetForm = () => {
    setName('');
    setEmail('');
    setRole('');
    setPhone('');
    setRegisteredDate('');
    setSuccessMessage('');
    setErrorMessage('');
  };

  return (
    <Box sx={{ p: 4, maxWidth: 500, mx: 'auto', textAlign: 'center' }}>
      <Button variant="outlined" onClick={() => navigate(-1)} sx={{ mb: 2 }}>
        Back
      </Button>
      <Typography variant="h4" gutterBottom color="primary">
        Add New User
      </Typography>
      <form onSubmit={handleSubmit}>
        <TextField label="Name" value={name} onChange={(e) => setName(e.target.value)} fullWidth required sx={{ mb: 2 }} />
        <TextField label="Email" type="email" value={email} onChange={(e) => setEmail(e.target.value)} fullWidth required sx={{ mb: 2 }} />
        <TextField select label="Role" value={role} onChange={(e) => setRole(e.target.value)} fullWidth required sx={{ mb: 2 }}>
          <MenuItem value="Admin">Admin</MenuItem>
          <MenuItem value="Observer">Observer</MenuItem>
          <MenuItem value="Citizen">Citizen</MenuItem>
          <MenuItem value="Data Analyst">Data Analyst</MenuItem>
        </TextField>
        <TextField label="Phone Number" value={phone} onChange={(e) => setPhone(e.target.value)} fullWidth required sx={{ mb: 2 }} />
        <TextField label="Registered Date" type="date" value={registeredDate} onChange={(e) => setRegisteredDate(e.target.value)} fullWidth required sx={{ mb: 2 }} InputLabelProps={{ shrink: true }} />
        <Button type="submit" variant="contained" color="primary" fullWidth>
          Add User
        </Button>
        <Button onClick={resetForm} variant="outlined" color="secondary" fullWidth sx={{ mt: 1 }}>
          Reset
        </Button>
      </form>
      {successMessage && <Typography color="success.main" sx={{ mt: 2 }}>{successMessage}</Typography>}
      {errorMessage && <Typography color="error.main" sx={{ mt: 2 }}>{errorMessage}</Typography>}
    </Box>
  );
};

export default AddUser;
