import axios from 'axios';

const API_URL = 'https://electionmonitoringbackend.up.railway.app'; // Update with your backend URL

// Function to fetch elections
export const getElections = async () => {
    try {
        const response = await axios.get(`${API_URL}/elections`);
        // Ensure response is as expected
        if (response.status === 200) {
            return response.data; // Return the fetched data
        }
        throw new Error('Failed to fetch elections: Unexpected response status');
    } catch (error) {
        console.error('Error fetching elections:', error);
        throw error; // Propagate the error
    }
};

// Function to create a new election
export const createElection = async (electionData) => {
    try {
        const response = await axios.post(`${API_URL}/elections`, electionData);
        // Ensure response is as expected
        if (response.status === 201) {
            return response.data; // Return the created election data
        }
        throw new Error('Failed to create election: Unexpected response status');
    } catch (error) {
        console.error('Error creating election:', error);
        throw error; // Propagate the error
    }
};

// Function to delete an election
export const deleteElection = async (electionId) => {
    try {
        const response = await axios.delete(`${API_URL}/elections/${electionId}`);
        return response.data; // Optional, based on your backend response
    } catch (error) {
        console.error('Error deleting election:', error);
        throw error; // Propagate the error
    }
};

// Function to update an election
export const updateElection = async (electionId, electionData) => {
    try {
        const response = await axios.put(`${API_URL}/elections/${electionId}`, electionData);
        return response.data; // Return the updated election data
    } catch (error) {
        console.error('Error updating election:', error);
        throw error; // Propagate the error
    }
};

