import React, { useState } from 'react';
//import { useNavigate } from 'react-router-dom';
import './SignUp.css'; // Make sure the filename matches exactly
import { addObserver } from '../services/api';

const SignUp = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [aadhaar, setAadhaar] = useState();
  const [mobile, setMobile] = useState();
  const [error, setError] = useState(''); // Used for error messages
  const [successMessage, setSuccessMessage] = useState(''); // Success message state
  //const navigate = useNavigate();

  const handleSignUp = async (e) => {
    e.preventDefault();
    const observer = {
      username: username,
      password: password,
      anum: aadhaar,
      mnum: mobile,
    };
    try {
      // Call the API to add the citizen
      await addObserver(observer);
      console.log('User added');
      setSuccessMessage('Sign-up successful!'); // Set success message
      setError(''); // Clear error message if the signup is successful
      // Redirect or take action after success, e.g., navigate to login or dashboard
      // navigate('/login'); // Uncomment if you want to navigate to the login page
    } catch (error) {
      console.log(error); // Handle the error
      setError('Something went wrong! Please try again.'); // Set error message
      setSuccessMessage(''); // Clear success message if there's an error
    }
  };

  return (
    <div className="signup-container">
      <div className="signup-form">
        <h2>Sign Up</h2>
        <form onSubmit={handleSignUp}>
          <div>
            <label>Username:</label>
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
            />
          </div>
          <div>
            <label>Password:</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <div>
            <label>Aadhaar Number:</label>
            <input
              type="text"
              value={aadhaar}
              onChange={(e) => setAadhaar(e.target.value)}
              maxLength={12}
              required
            />
          </div>
          <div>
            <label>Mobile Number:</label>
            <input
              type="text"
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
              maxLength={10}
              required
            />
          </div>
          {error && <p className="error">{error}</p>}
          {successMessage && <p className="success">{successMessage}</p>} {/* Display success message */}
          <div>
            <button type="submit">Sign Up</button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SignUp;
