import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate hook
import './DataVisualization.css'; // Import CSS for styling

const DataVisualization = () => {
  const navigate = useNavigate(); // Initialize the navigate function

  return (
    <div className="data-visualization-container">
      {/* Back button */}
      <button className="back-button" onClick={() => navigate('/observer')}>Back</button>

      <h2>Live Data Visualization Tools</h2>
      <p>Welcome to the Live Data Visualization Tools section. Here, you can visualize election data in real time.</p>

      {/* Example content for the visualization tools */}
      <section className="charts-section">
        <h3>Charts and Graphs</h3>
        <p>Below are some visual representations of the election data:</p>
        <div className="charts-container">
          {/* Placeholder for charts */}
          <div className="chart">
            <h4>Voting Trends Over Time</h4>
            {/* Placeholder for an actual chart */}
            <p>[Chart will be displayed here]</p>
          </div>
          <div className="chart">
            <h4>Voter Demographics</h4>
            {/* Placeholder for another chart */}
            <p>[Chart will be displayed here]</p>
          </div>
        </div>
      </section>

      <section className="data-tables">
        <h3>Data Tables</h3>
        <p>Review detailed data below:</p>
        <table className="data-table">
          <thead>
            <tr>
              <th>Polling Station</th>
              <th>Votes Counted</th>
              <th>Issues Reported</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Station 1</td>
              <td>150</td>
              <td>None</td>
            </tr>
            <tr>
              <td>Station 2</td>
              <td>200</td>
              <td>Minor delays</td>
            </tr>
            <tr>
              <td>Station 3</td>
              <td>120</td>
              <td>None</td>
            </tr>
          </tbody>
        </table>
      </section>
    </div>
  );
};

export default DataVisualization;
