import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './ViewResults.css'; // Import the CSS file

const ViewResults = () => {
  const [parties, setParties] = useState([]);

  useEffect(() => {
    axios.get('https://electionmonitoringbackend.up.railway.app/api/parties')
      .then((response) => {
        setParties(response.data);
      })
      .catch((error) => {
        console.error('Error fetching parties:', error);
      });
  }, []);

  return (
    <div className="view-results-container">
      <h1 className="animated-title">View Results</h1>
      <table className="party-table">
        <thead>
          <tr>
            <th>Party Name</th>
            <th>Symbol</th>
            <th>Participant Name</th>
            <th>Votes</th>
          </tr>
        </thead>
        <tbody>
          {parties.map((party) => (
            <tr key={party.id} className="party-row">
              <td>{party.name}</td>
              <td className="party-symbol-cell">{party.symbol}</td> {/* Display the symbol directly */}
              <td>{party.participantName}</td>
              <td>{party.votes}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default ViewResults;
