import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate
import './DataAnalyst.css'; // Import the CSS file

const DataAnalyst = () => {
  const navigate = useNavigate(); // Initialize the navigate function
  const handleLogout = () => {
    navigate('/access'); // Redirect to the Admin Login page
  };


  return (
    <div className="data-analyst-container">
      {/* Update back button to navigate back in history */}
      <button className="logout-button" onClick={handleLogout}>
          Logout
        </button>

      <h2>Data Analyst Dashboard</h2>
      <p className="welcome-message">
        Welcome to the Data Analyst section of the Election Monitoring System. Your role is crucial in processing and interpreting election data to ensure a fair and transparent electoral process.
      </p>

      <section className="analysis-section card">
        <h3>Data Analysis</h3>
        <p>Perform in-depth analyses of voting trends and patterns.</p>
        <button className="action-button" onClick={() => navigate('/voter-engagement-report')}>View Voter Engagement Report</button>
        <button className="action-button" onClick={() => navigate('/demographic-analysis')}>View Demographic Analysis</button>
        <button className="action-button" onClick={() => navigate('/polling-station-issues')}>View Polling Station Issues</button>
      </section>

      <section className="footer">
        <h3>Analyst Support</h3>
        <p>If you need assistance, please contact support at <a href="mailto:support@example.com">support@example.com</a>.</p>
      </section>
    </div>
  );
};

export default DataAnalyst;
