import React, { useState, useEffect } from 'react';
import axios from 'axios';

const VoterManagement = () => {
  const [voters, setVoters] = useState([]);
  const [voter, setVoter] = useState({
    name: '',
    email: '',
    address: ''
  });

  // Fetch voters when the component mounts
  useEffect(() => {
    axios.get('https://electionmonitoringbackend.up.railway.app/api/voters')
      .then((response) => {
        setVoters(response.data);
      })
      .catch((error) => {
        console.error('Error fetching voters:', error);
      });
  }, []);

  // Handle form input change
  const handleChange = (e) => {
    setVoter({
      ...voter,
      [e.target.name]: e.target.value
    });
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();

    axios.post('https://electionmonitoringbackend.up.railway.app/api/voters', voter)
      .then((response) => {
        setVoters([...voters, response.data]);
        setVoter({ name: '', email: '', address: '' });
      })
      .catch((error) => {
        console.error('Error adding voter:', error);
      });
  };

  // Handle delete voter
  const handleDelete = (id) => {
    axios.delete(`https://electionmonitoringbackend.up.railway.app/api/voters/${id}`)
      .then(() => {
        setVoters(voters.filter(voter => voter.id !== id));
      })
      .catch((error) => {
        console.error('Error deleting voter:', error);
      });
  };

  return (
    <div>
      <h2>Voter Management</h2>

      {/* Add Voter Form */}
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          name="name"
          value={voter.name}
          onChange={handleChange}
          placeholder="Name"
          required
        />
        <input
          type="email"
          name="email"
          value={voter.email}
          onChange={handleChange}
          placeholder="Email"
          required
        />
        <input
          type="text"
          name="address"
          value={voter.address}
          onChange={handleChange}
          placeholder="Address"
          required
        />
        <button type="submit">Add Voter</button>
      </form>

      {/* Voters List */}
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <th>Address</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {voters.map((voter) => (
            <tr key={voter.id}>
              <td>{voter.name}</td>
              <td>{voter.email}</td>
              <td>{voter.address}</td>
              <td>
                <button onClick={() => handleDelete(voter.id)}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default VoterManagement;
