import React from 'react';
import { Box, Button, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const AboutUs = () => {
  const navigate = useNavigate(); // Initialize the useNavigate hook

  return (
    <Box sx={{ p: 4 }}>
      <Button
        variant="contained"
        onClick={() => navigate(-1)}
        sx={{
          mb: 2,
          backgroundColor: '#1976d2',
          color: '#fff',
          border: '1px solid #115293',
          ':hover': {
            backgroundColor: '#115293',
          },
        }}
      >
        Back
      </Button>

      <Typography variant="h2" gutterBottom>
        About Us
      </Typography>
      <Typography variant="body1" paragraph>
        The Election Monitoring System is dedicated to promoting transparency and trust in the electoral process.
        By leveraging technology, we provide citizens, election observers, and officials with tools to track and monitor
        elections in real-time, ensuring that the democratic process is respected.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Our Mission
      </Typography>
      <Typography variant="body2" paragraph>
        Our mission is to empower citizens and stakeholders by providing accessible and reliable information about
        elections, enhancing civic engagement, and fostering accountability.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Our Vision
      </Typography>
      <Typography variant="body2" paragraph>
        We envision a world where elections are conducted transparently, free from fraud, and where every vote counts.
        Our goal is to create a trusted platform that enables informed participation in the democratic process.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Join Us
      </Typography>
      <Typography variant="body2" paragraph>
        We invite you to join us in our mission by participating in elections, becoming an observer, or simply staying informed
        about the electoral process. Together, we can ensure fair and democratic elections for all.
      </Typography>
    </Box>
  );
};

export default AboutUs;
