// src/components/ElectionList.js
import React, { useEffect, useState } from 'react';
import { getElections } from '../services/apiService'; // Ensure correct path to apiService
import ElectionForm from './ElectionForm'; // Import the ElectionForm component

const ElectionList = () => {
    const [elections, setElections] = useState([]); // State to hold the list of elections
    const [loading, setLoading] = useState(true); // State to manage loading status
    const [error, setError] = useState(null); // State to manage error messages

    const fetchElections = async () => {
        setLoading(true); // Set loading to true while fetching
        try {
            const data = await getElections(); // Call the API function to get elections
            setElections(data); // Update state with the fetched elections
        } catch (err) {
            setError('Failed to fetch elections.'); // Set error message if fetching fails
        } finally {
            setLoading(false); // Set loading to false once fetching is complete
        }
    };

    useEffect(() => {
        fetchElections(); // Fetch elections when the component mounts
    }, []); // The empty array ensures this runs only once when the component mounts

    const handleElectionCreated = () => {
        fetchElections(); // Refresh the election list after creating a new election
    };

    if (loading) {
        return <div>Loading...</div>; // Show a loading message while fetching
    }

    if (error) {
        return <div>{error}</div>; // Show an error message if there was an error
    }

    return (
        <div>
            <h1>Elections</h1>
            <ElectionForm onElectionCreated={handleElectionCreated} /> {/* Render the ElectionForm */}
            <ul>
                {elections.map((election) => (
                    <li key={election.id}>{election.name} - {election.election_date}</li> // Render each election
                ))}
            </ul>
        </div>
    );
};

export default ElectionList; // Export the component for use in other parts of the app
