import React from 'react';
import './Access.css'; // Assuming your CSS file is named Access.css
import { useNavigate } from 'react-router-dom';

const Access = () => {
  const navigate = useNavigate();

  // Navigate to login or signup based on the role
  const handleNavigate = (type, role) => {
    if (type === 'login') {
      navigate(`/login/${role.toLowerCase()}`); // Redirect to login page of the selected role
    } else if (type === 'signup') {
      navigate(`/signup/${role.toLowerCase()}`); // Redirect to signup page of the selected role
    }
  };

  // Function for handling the Back button click
  const handleBack = () => {
    navigate('/'); // Go back to the Access page
  };

  return (
    <div className="access-container">
      <div className="access-card">
        <h3>Admin</h3>
        <button
          className="access-button"
          onClick={() => handleNavigate('login', 'Admin')}
        >
          Login
        </button>
        
      </div>

      <div className="access-card">
        <h3>Citizen</h3>
        <button
          className="access-button"
          onClick={() => handleNavigate('login', 'Citizen')}
        >
          Login
        </button>
        <button
          className="access-button"
          onClick={() => handleNavigate('signup', 'Citizen')}
        >
          Sign Up
        </button>
      </div>

      <div className="access-card">
        <h3>Election Observer</h3>
        <button
          className="access-button"
          onClick={() => handleNavigate('login', 'Observer')}
        >
          Login
        </button>
        <button
          className="access-button"
          onClick={() => handleNavigate('signup', 'Observer')}
        >
          Sign Up
        </button>
      </div>

      <div className="access-card">
        <h3>Data Analyst</h3>
        <button
          className="access-button"
          onClick={() => handleNavigate('login', 'Analyst')}
        >
          Login
        </button>
        <button
          className="access-button"
          onClick={() => handleNavigate('signup', 'Analyst')}
        >
          Sign Up
        </button>
      </div>

      {/* Back button that navigates to the main page */}
      <button className="access-button back-button" onClick={handleBack}>
        Back to Home
      </button>
    </div>
  );
};

export default Access;
