import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './ReportIssue.css';

const ReportIssue = () => {
  const navigate = useNavigate();
  const [issueDescription, setIssueDescription] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await axios.post('https://electionmonitoringbackend.up.railway.app/api/issues', {
        description: issueDescription,
      });
      alert('Issue reported successfully!');
      setIssueDescription(''); // Clear input after submission
      navigate('/citizen'); // Redirect after successful submission
    } catch (error) {
      console.error('Error reporting issue:', error);
      alert('Failed to report issue. Please try again later.');
    }
  };

  return (
    <div className="report-issue-container">
      <button className="back-button" onClick={() => navigate('/citizen')}>Back</button>
      <h2>Report Election Issue</h2>
      <p>Use this page to report any issues related to elections.</p>
      <form onSubmit={handleSubmit} className="report-issue-form">
        <label htmlFor="issue-description">Issue Description:</label>
        <textarea
          id="issue-description"
          value={issueDescription}
          onChange={(e) => setIssueDescription(e.target.value)}
          rows="4"
          required
          placeholder="Describe the issue..."
        />
        <button type="submit" className="submit-button">Submit Report</button>
      </form>
      <div className="footer">
        <p>If you have any urgent concerns, please contact the election office directly.</p>
      </div>
    </div>
  );
};

export default ReportIssue;
