import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate hook
import './CommunicationModule.css'; // Import the CSS file

const CommunicationModule = () => {
  const navigate = useNavigate(); // Initialize the navigate function

  return (
    <div className="communication-module-container">
      {/* Back button */}
      <button className="back-button" onClick={() => navigate('/observer')}>Back</button>

      <h2>Communication Channels with Officials</h2>

      <div className="communication-channels">
        <div className="channel">
          <h3>Email Communication</h3>
          <p>
            For urgent matters, you can contact election officials directly via email. 
            This is the preferred method for detailed inquiries. Please include your name, 
            contact information, and a clear subject line for faster response.
          </p>
          <p>
            Example: <strong>support@example.com</strong>
          </p>
        </div>

        <div className="channel">
          <h3>Dedicated Hotline</h3>
          <p>
            Our dedicated hotline is available 24/7 for immediate assistance. 
            You can call to report issues, seek clarification, or request support.
          </p>
          <p>
            Example: <strong>1-800-123-4567</strong>
          </p>
        </div>

        <div className="channel">
          <h3>Chat Support</h3>
          <p>
            Use our chat support feature for real-time communication with officials. 
            This option is ideal for quick questions or to get immediate help while 
            navigating the election process.
          </p>
          <p>
            Available on our website during business hours.
          </p>
        </div>

        <div className="channel">
          <h3>Feedback Forms</h3>
          <p>
            We value your feedback! Submit any issues encountered or suggestions 
            for improvement through our online forms. Your input helps us enhance 
            the election experience for everyone.
          </p>
          <p>
            Access the feedback form here: <strong><a href="/feedback-form">Feedback Form</a></strong>
          </p>
        </div>
      </div>

      <div className="footer">
        <p>If you have any questions, please reach out to <a href="mailto:support@example.com">support@example.com</a>.</p>
      </div>
    </div>
  );
};

export default CommunicationModule;
