// src/pages/VoterStatus.js
import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate hook
import './VoterStatus.css'; // Import CSS for styling

const VoterStatus = () => {
  const navigate = useNavigate(); // Initialize the navigate function

  // Sample voter status information (replace with real data as needed)
  const voterInfo = {
    name: "John Doe",
    status: "Registered",
    pollingStation: "Downtown Community Center",
    votingDistrict: "District 5",
  };

  return (
    <div className="voter-status-container">
      {/* Back button */}
      <button className="back-button" onClick={() => navigate('/citizen')}>Back</button>

      <h2>Check Voter Status</h2>
      <p>Your voter status information is displayed below:</p>

      <div className="voter-info">
        <p><strong>Name:</strong> {voterInfo.name}</p>
        <p><strong>Status:</strong> {voterInfo.status}</p>
        <p><strong>Polling Station:</strong> {voterInfo.pollingStation}</p>
        <p><strong>Voting District:</strong> {voterInfo.votingDistrict}</p>
      </div>

      <div className="footer">
        <p>If you have questions about your voter status, please contact your local election office.</p>
      </div>
    </div>
  );
};

export default VoterStatus;
