import React, { useEffect, useState } from 'react';
import { getElections, deleteElection, updateElection } from '../services/apiService';
import './ViewElections.css';

const ViewElections = () => {
    const [elections, setElections] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [editingId, setEditingId] = useState(null);
    const [editedElection, setEditedElection] = useState({});

    useEffect(() => {
        const fetchElections = async () => {
            try {
                const response = await getElections();
                setElections(response);
            } catch (error) {
                console.error('Error fetching elections:', error);
                setError('Failed to load elections. Please try again later.');
            } finally {
                setLoading(false);
            }
        };

        fetchElections();
    }, []);

    const handleDelete = async (electionId) => {
        if (window.confirm('Are you sure you want to delete this election?')) {
            try {
                await deleteElection(electionId);
                setElections(prevElections => prevElections.filter(election => election.id !== electionId));
            } catch (error) {
                console.error('Error deleting election:', error);
            }
        }
    };

    const handleEdit = (election) => {
        setEditingId(election.id);
        setEditedElection({ ...election });
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setEditedElection(prev => ({
            ...prev,
            [name]: value,
        }));
    };

    const handleSave = async () => {
        if (!editedElection.name || !editedElection.electionDate || !editedElection.location || !editedElection.status) {
            alert("All fields are required!");
            return;
        }

        try {
            const updatedElection = await updateElection(editedElection.id, editedElection);
            setElections(prevElections =>
                prevElections.map(election =>
                    election.id === updatedElection.id ? updatedElection : election
                )
            );
            setEditingId(null);
        } catch (error) {
            console.error('Error updating election:', error);
        }
    };

    return (
        <div className="view-elections-container">
            <h2>View Elections</h2>
            {loading ? (
                <p>Loading elections...</p>
            ) : error ? (
                <p className="error-message">{error}</p>
            ) : elections.length > 0 ? (
                <ul className="elections-list">
                    {elections.map((election) => (
                        <li key={election.id} className="election-item">
                            <div className="election-details">
                                {editingId === election.id ? (
                                    <>
                                        <input
                                            type="text"
                                            name="name"
                                            value={editedElection.name}
                                            onChange={handleChange}
                                        />
                                        <input
                                            type="date"
                                            name="electionDate"
                                            value={editedElection.electionDate.split('T')[0]}
                                            onChange={handleChange}
                                        />
                                        <input
                                            type="text"
                                            name="location"
                                            value={editedElection.location}
                                            onChange={handleChange}
                                        />
                                        <input
                                            type="text"
                                            name="status"
                                            value={editedElection.status}
                                            onChange={handleChange}
                                        />
                                        <button className="save-button" onClick={handleSave}>Save</button>
                                    </>
                                ) : (
                                    <>
                                        <h3>{election.name}</h3>
                                        <p>Date: {election.electionDate}</p>
                                        <p>Location: {election.location}</p>
                                        <p>Status: {election.status}</p>
                                        <button className="edit-button" onClick={() => handleEdit(election)}>Update</button>
                                    </>
                                )}
                            </div>
                            <button className="delete-button" onClick={() => handleDelete(election.id)}>Delete</button>
                        </li>
                    ))}
                </ul>
            ) : (
                <p>No elections found.</p>
            )}
        </div>
    );
};

export default ViewElections;
