// src/pages/HomePage.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import './HomePage.css';
import regimg from '../images/regimg.jpg';
import mainimg from '../images/mainimg.jpeg';

const HomePage = () => {
  const navigate = useNavigate();

  const handleSignUpClick = () => {
    navigate('/access'); // Navigate to the Access page
  };

  const handleLearnMoreAboutUs = () => {
    navigate('/about-us'); // Navigate to the About Us page
  };

  const handleLearnMoreResources = () => {
    navigate('/resources'); // Navigate to the Resources page
  };

  const handleGetSupport = () => {
    navigate('/support'); // Navigate to the Support page
  };

  const handleViewElections = () => {
    navigate('/citizenViewElection'); // Navigate to the View Elections page
  };

  return (
    <div className="homepage-container">
      <h2 className="homepage-title">Welcome to the Election Monitoring System</h2>

      <section className="intro animated-section">
        <div className="intro-content">
          <h3>About Us</h3>
          <p>
            The Election Monitoring System is dedicated to promoting transparency and trust in the electoral process.
            By leveraging technology, we provide citizens, election observers, and officials with tools to track and
            monitor elections in real-time, ensuring that the democratic process is respected.
          </p>
          <button className="learn-more-button" onClick={handleLearnMoreAboutUs}>
            Learn More
          </button>
        </div>
        <img src={mainimg} alt="Election Monitoring" className="intro-image" />
      </section>

      <h3>Core Features</h3>

      <section className="additional-containers">
        <div className="container-item">
          <h3>Upcoming Elections</h3>
          <p>Stay informed about upcoming elections in your region. Learn about candidate profiles, key dates, and voter registration deadlines to make an informed decision.</p>
          <button className="container-button" onClick={handleViewElections}>View Elections</button>
        </div>
        <div className="container-item">
          <h3>Volunteer</h3>
          <p>Become a volunteer to help monitor elections and ensure a fair and transparent process. Your involvement can make a difference in maintaining the integrity of democracy.</p>
          <button className="container-button" onClick={handleSignUpClick}>
            Sign Up
          </button>
        </div>
        <div className="container-item">
          <h3>Support</h3>
          <p>If you have questions or face issues while using our platform, our dedicated support team is here to help. Reach out for assistance at any time.</p>
          <button className="container-button" onClick={handleGetSupport}>
            Get Support
          </button>
        </div>
        <div className="container-item">
          <h3>Resources</h3>
          <p>Explore our extensive resources on election monitoring, including guides, articles, and case studies. Empower yourself with the knowledge to observe elections effectively.</p>
          <button className="container-button" onClick={handleLearnMoreResources}>
            Learn More
          </button>
        </div>
      </section>

      <section className="get-involved animated-section">
        <h3>Get Involved</h3>
        <p>Be an active participant in ensuring a fair electoral process. Here’s how you can contribute:</p>
        <ul>
          <li><strong>Register to vote</strong> and encourage others to participate in the democratic process.</li>
          <li><strong>Report irregularities</strong> or any suspicious activity you observe during the election.</li>
          <li><strong>Stay informed</strong> on key election dates and procedures to exercise your rights effectively.</li>
          <li><strong>Engage in discussions</strong> about election integrity and contribute to spreading awareness.</li>
        </ul>
        <img src={regimg} alt="Get Involved" className="observer-image" />
        <button className="get-involved-button">Get Involved</button>
      </section>

      <footer className="footer">
        <p>For assistance, contact <a href="mailto:support@example.com">support@example.com</a>.</p>
      </footer>
    </div>
  );
};

export default HomePage;
