import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { createElection } from '../services/apiService'; // Ensure the correct path is used

const CreateElection = () => {
    const [electionName, setElectionName] = useState('');
    const [electionDate, setElectionDate] = useState('');
    const [electionLocation, setElectionLocation] = useState('');
    const [electionStatus, setElectionStatus] = useState('Upcoming');
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        const electionData = {
            name: electionName,
            electionDate: electionDate,
            location: electionLocation,
            status: electionStatus,
        };

        try {
            await createElection(electionData);
            navigate('/'); // Redirect after creation
        } catch (error) {
            console.error('Error creating election:', error);
        }
    };

    return (
        <div className="create-election-form">
            <h2>Create New Election</h2>
            <form onSubmit={handleSubmit}>
                <div>
                    <label htmlFor="name">Election Name:</label>
                    <input
                        type="text"
                        id="name"
                        value={electionName}
                        onChange={(e) => setElectionName(e.target.value)}
                        required
                    />
                </div>
                <div>
                    <label htmlFor="date">Election Date:</label>
                    <input
                        type="date"
                        id="date"
                        value={electionDate}
                        onChange={(e) => setElectionDate(e.target.value)}
                        required
                    />
                </div>
                <div>
                    <label htmlFor="location">Location:</label>
                    <input
                        type="text"
                        id="location"
                        value={electionLocation}
                        onChange={(e) => setElectionLocation(e.target.value)}
                        required
                    />
                </div>
                <div>
                    <label htmlFor="status">Status:</label>
                    <select
                        id="status"
                        value={electionStatus}
                        onChange={(e) => setElectionStatus(e.target.value)}
                    >
                        <option value="Upcoming">Upcoming</option>
                        <option value="Ongoing">Ongoing</option>
                        <option value="Completed">Completed</option>
                    </select>
                </div>
                <button type="submit">Create Election</button>
            </form>
        </div>
    );
};

export default CreateElection;
