import React from 'react';
import { Box, Button, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const Resources = () => {
  const navigate = useNavigate(); // Initialize the useNavigate hook

  return (
    <Box sx={{ p: 4 }}>
      <Button
        variant="contained"
        onClick={() => navigate(-1)}
        sx={{
          mb: 2,
          backgroundColor: '#1976d2',
          color: '#fff',
          border: '1px solid #115293',
          ':hover': {
            backgroundColor: '#115293',
          },
        }}
      >
        Back
      </Button>

      <Typography variant="h2" gutterBottom>
        Resources
      </Typography>
      <Typography variant="body1" paragraph>
        Explore our extensive resources on election monitoring, including guides, articles, and case studies.
        Empower yourself with the knowledge to observe elections effectively.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Guides
      </Typography>
      <Typography variant="body2" paragraph>
        - **Guide to Election Observation**: A comprehensive guide that outlines the role of election observers and best practices.
      </Typography>
      <Typography variant="body2" paragraph>
        - **Voter Education Resources**: Tools and materials to help educate voters about the electoral process and their rights.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Articles
      </Typography>
      <Typography variant="body2" paragraph>
        - **The Importance of Transparent Elections**: An article discussing why transparency is vital for democratic processes.
      </Typography>
      <Typography variant="body2" paragraph>
        - **Technological Innovations in Election Monitoring**: Insights into how technology is changing the landscape of election monitoring.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Case Studies
      </Typography>
      <Typography variant="body2" paragraph>
        - **Case Study: 2020 General Elections**: An analysis of the election monitoring efforts during the 2020 elections.
      </Typography>
      <Typography variant="body2" paragraph>
        - **Impact of Social Media on Elections**: A case study on the role of social media in influencing voter behavior and election outcomes.
      </Typography>

      <Typography variant="body1">
        Stay informed and engaged by utilizing these resources to ensure fair and transparent elections.
      </Typography>
    </Box>
  );
};

export default Resources;
