import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate hook
import './IncidentReporting.css'; // Import CSS for styling if necessary

const IncidentReporting = () => {
  const navigate = useNavigate(); // Initialize the navigate function
  const [incidentDetails, setIncidentDetails] = useState(''); // State to hold incident details

  const handleSubmit = (e) => {
    e.preventDefault();
    // Here, you would handle the form submission logic, e.g., send data to your backend
    console.log('Incident reported:', incidentDetails);
    // Clear the form after submission or navigate to a different page
    setIncidentDetails('');
  };

  return (
    <div className="incident-reporting-container">
      {/* Back button */}
      <button className="back-button" onClick={() => navigate('/observer')}>Back</button>

      <h2>Incident Reporting Form</h2>
      <p>Please fill in the details of the incident you wish to report.</p>

      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="incident-details">Incident Details:</label>
          <textarea
            id="incident-details"
            value={incidentDetails}
            onChange={(e) => setIncidentDetails(e.target.value)}
            required
            rows="5"
            placeholder="Describe the incident..."
          ></textarea>
        </div>
        
        <div className="form-group">
          <label htmlFor="incident-date">Date of Incident:</label>
          <input
            type="date"
            id="incident-date"
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="incident-location">Location:</label>
          <input
            type="text"
            id="incident-location"
            required
            placeholder="Enter location"
          />
        </div>

        <button type="submit" className="submit-button">Submit Report</button>
      </form>
    </div>
  );
};

export default IncidentReporting;
