import React, { useEffect, useState } from 'react';
import { Container, Typography, Box } from '@mui/material';
import axios from 'axios';

const SystemSettings = () => {
  const [settings, setSettings] = useState({});

  useEffect(() => {
    // Fetch settings from backend
    axios
      .get('https://electionmonitoringbackend.up.railway.app/api/settings')
      .then((response) => {
        setSettings(response.data);
      })
      .catch((error) => {
        console.error('Error fetching settings:', error);
        alert('Failed to load settings.');
      });
  }, []);

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 5, p: 3, boxShadow: 3, borderRadius: 2, backgroundColor: '#fff' }}>
        <Typography variant="h4" align="center" gutterBottom>
          System Settings
        </Typography>

        {settings ? (
          <Box>
            <Typography variant="h6">Application Title: {settings.appTitle}</Typography>
            <Typography variant="h6">Notification Email: {settings.notificationEmail}</Typography>
            <Typography variant="h6">Election Reminder: {settings.electionReminder} days</Typography>
            <Typography variant="h6">Time Zone: {settings.timeZone}</Typography>
            <Typography variant="h6">Default Language: {settings.defaultLanguage}</Typography>
          </Box>
        ) : (
          <Typography>No settings available.</Typography>
        )}
      </Box>
    </Container>
  );
};

export default SystemSettings;
