import React, { useState, useEffect } from 'react';
import { Button, TextField, Typography, Container, Box, FormControl, InputLabel, Select, MenuItem } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './UpdateSettings.css';


const UpdateSettings = () => {
  const navigate = useNavigate();
  const [settings, setSettings] = useState({
    appTitle: '',
    notificationEmail: '',
    electionReminder: '',
    timeZone: '',
    defaultLanguage: '',
  });

  useEffect(() => {
    // Fetch existing settings from the backend on component load
    axios
      .get('https://electionmonitoringbackend.up.railway.app/api/settings')
      .then((response) => {
        setSettings(response.data);
      })
      .catch((error) => {
        console.error('Error fetching settings:', error);
        alert('Failed to load settings. Please try again later.');
      });
  }, []);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setSettings({ ...settings, [name]: value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    axios
      .post('http://localhost:8080/api/settings', settings)
      .then((response) => {
        alert('Settings updated successfully!');
        console.log('Updated settings:', response.data);
      })
      .catch((error) => {
        console.error('Error updating settings:', error);
        alert('Failed to update settings. Please try again.');
      });
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 5, p: 3, boxShadow: 3, borderRadius: 2, backgroundColor: '#fff' }}>
        <Typography variant="h4" align="center" gutterBottom>
          Update System Settings
        </Typography>

        {/* Back Button */}
        <Button variant="outlined" color="secondary" onClick={() => navigate(-1)} sx={{ mb: 2 }}>
          Back
        </Button>

        <form onSubmit={handleSubmit}>
          <TextField
            label="Application Title"
            name="appTitle"
            value={settings.appTitle}
            onChange={handleChange}
            fullWidth
            margin="normal"
            required
          />
          <TextField
            label="Notification Email"
            name="notificationEmail"
            value={settings.notificationEmail}
            onChange={handleChange}
            fullWidth
            margin="normal"
            type="email"
            required
          />
          <TextField
            label="Election Reminder (in days)"
            name="electionReminder"
            value={settings.electionReminder}
            onChange={handleChange}
            fullWidth
            margin="normal"
            type="number"
            required
            inputProps={{ min: 1 }}
          />

          {/* Time Zone Selection */}
          <FormControl fullWidth margin="normal">
            <InputLabel>Time Zone</InputLabel>
            <Select name="timeZone" value={settings.timeZone} onChange={handleChange} required>
              <MenuItem value="UTC-12:00">UTC-12:00</MenuItem>
              <MenuItem value="UTC+00:00">UTC+00:00</MenuItem>
              <MenuItem value="UTC+05:30">UTC+05:30 (IST)</MenuItem>
              <MenuItem value="UTC+12:00">UTC+12:00</MenuItem>
              {/* Add more time zones as needed */}
            </Select>
          </FormControl>

          {/* Default Language Selection */}
          <FormControl fullWidth margin="normal">
            <InputLabel>Default Language</InputLabel>
            <Select name="defaultLanguage" value={settings.defaultLanguage} onChange={handleChange} required>
              <MenuItem value="English">English</MenuItem>
              <MenuItem value="Spanish">Spanish</MenuItem>
              <MenuItem value="French">French</MenuItem>
              <MenuItem value="German">German</MenuItem>
              <MenuItem value="Chinese">Chinese</MenuItem>
            </Select>
          </FormControl>

          <Box mt={3} display="flex" justifyContent="center">
            <Button type="submit" variant="contained" color="primary">
              Save Settings
            </Button>
          </Box>
        </form>
      </Box>
    </Container>
  );
};

export default UpdateSettings;
