// src/components/ElectionForm.js
import React, { useState } from 'react';
import { createElection } from '../services/apiService'; // Ensure correct path to apiService

const ElectionForm = ({ onElectionCreated }) => {
    const [name, setName] = useState(''); // State for election name
    const [date, setDate] = useState(''); // State for election date
    const [error, setError] = useState(null); // State for error messages
    const [loading, setLoading] = useState(false); // State for loading status

    const handleSubmit = async (e) => {
        e.preventDefault(); // Prevent default form submission
        setLoading(true); // Set loading to true while processing

        try {
            const electionData = { name, date }; // Prepare data for submission
            await createElection(electionData); // Call the API to create the election
            onElectionCreated(); // Call the parent callback to refresh the election list
            setName(''); // Reset form fields
            setDate('');
            setError(null); // Clear error
        } catch (err) {
            setError('Failed to create election.'); // Set error message if creation fails
        } finally {
            setLoading(false); // Set loading to false once processing is complete
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <h2>Create New Election</h2>
            <div>
                <label htmlFor="name">Election Name:</label>
                <input
                    type="text"
                    id="name"
                    value={name}
                    onChange={(e) => setName(e.target.value)} // Update state on input change
                    required
                />
            </div>
            <div>
                <label htmlFor="date">Election Date:</label>
                <input
                    type="date"
                    id="date"
                    value={date}
                    onChange={(e) => setDate(e.target.value)} // Update state on input change
                    required
                />
            </div>
            <button type="submit" disabled={loading}>
                {loading ? 'Creating...' : 'Create Election'}
            </button>
            {error && <p style={{ color: 'red' }}>{error}</p>} {/* Show error if exists */}
        </form>
    );
};

export default ElectionForm; // Export the component for use in other parts of the app
