// Removed the unused state variables
import React, { useState,useEffect } from 'react';
import axios from 'axios'
import { useNavigate } from 'react-router-dom';
import './PollVote.css'; // Ensure to style the component

const PollVote = () => {
  const [selectedParty, setSelectedParty] = useState(null);
  const [parties,setParties]=useState([])
  const navigate = useNavigate();

  useEffect(() => {
    axios.get('http://localhost:8080/api/parties')
      .then((response) => {
        setParties(response.data);
      })
      .catch((error) => {
        console.error('Error fetching parties:', error);
      });
  }, []);

  const handleVoteSubmit = () => {
    if (selectedParty) {
      alert(`You have successfully voted for ${selectedParty}`);
      axios.post(`https://electionmonitoringbackend.up.railway.app/api/parties/${selectedParty.id}/votes`)
      .then((response) => {
        console.log(' votes added')
      })
      .catch((error) => {
        console.error('Error fetching parties:', error);
      });
      navigate('/citizen'); // Navigate to the correct route
    } else {
      alert('Please select a party to vote for.');
    }
  };

  return (
    <div className="poll-vote-container">
      <h2>Poll Vote</h2>
      <p>Select a party and vote for your preferred leader:</p>

      <div className="party-list">
        {parties.map((party) => (
          <div
            key={party.id}
            className={`party-card ${selectedParty === party.name ? 'selected' : ''}`}
            onClick={() => setSelectedParty(party)}
          >
            <h3>{party.name}</h3>
            <p>Leader: {party.participantName}</p>
          </div>
        ))}
      </div>

      <button className="submit-vote-button" onClick={handleVoteSubmit}>
        Submit Vote
      </button>

      {/* Back button now correctly navigates to the citizen dashboard */}
      <button className="back-button" onClick={() => navigate('/citizen')}>
        Back to Dashboard
      </button>
    </div>
  );
};

export default PollVote;
