import React, { useState, useEffect } from 'react';
import axios from 'axios';

const ViewParties = () => {
  const [parties, setParties] = useState([]);
  const [newParty, setNewParty] = useState({
    name: '',
    symbol: '',
    participantName: ''
  });

  // Fetch parties from the backend
  useEffect(() => {
    axios.get('https://electionmonitoringbackend.up.railway.app/api/parties')
      .then((response) => {
        setParties(response.data);
      })
      .catch((error) => {
        console.error('Error fetching parties:', error);
      });
  }, []);

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewParty({
      ...newParty,
      [name]: value
    });
  };

  // Add a new party
  const handleAddParty = (e) => {
    e.preventDefault();
    axios.post('https://electionmonitoringbackend.up.railway.app/api/parties', newParty)
      .then((response) => {
        setParties([...parties, response.data]);
        setNewParty({ name: '', symbol: '', participantName: '' }); // Clear the form
      })
      .catch((error) => {
        console.error('Error adding party:', error);
      });
  };

  // Delete a party
  const handleDeleteParty = (id) => {
    axios.delete(`https://electionmonitoringbackend.up.railway.app/api/parties/${id}`)
      .then(() => {
        setParties(parties.filter((party) => party.id !== id));
      })
      .catch((error) => {
        console.error('Error deleting party:', error);
      });
  };

  return (
    <div className="view-parties-container">
      <h2>All Parties</h2>
      <p>Manage and view all political parties here.</p>

      {/* Add Party Form */}
      <form onSubmit={handleAddParty} className="add-party-form">
        <div>
          <input
            type="text"
            name="name"
            value={newParty.name}
            onChange={handleInputChange}
            placeholder="Party Name"
            required
          />
          <input
            type="text"
            name="symbol"
            value={newParty.symbol}
            onChange={handleInputChange}
            placeholder="Symbol"
            required
          />
          <input
            type="text"
            name="participantName"
            value={newParty.participantName}
            onChange={handleInputChange}
            placeholder="Participant Name"
            required
          />
          <button type="submit">Add Party</button>
        </div>
      </form>

      {/* Party List Table */}
      <table className="party-table">
        <thead>
          <tr>
            <th>Party Name</th>
            <th>Symbol</th>
            <th>Participant Name</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {parties.map((party) => (
            <tr key={party.id}>
              <td>{party.name}</td>
              <td>{party.symbol}</td>
              <td>{party.participantName}</td>
              <td>
                <button onClick={() => handleDeleteParty(party.id)}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default ViewParties;
