// src/components/PollingIssues.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Button,
  TextField,
  Typography,
  Container,
  Box,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from '@mui/material';

const PollingIssues = () => {
  const navigate = useNavigate();

  const [issues, setIssues] = useState([
    { id: 1, description: 'Long waiting lines', category: 'Queue Management', reportedBy: 'Observer A', status: 'Pending' },
    { id: 2, description: 'Malfunctioning voting machines', category: 'Equipment', reportedBy: 'Observer B', status: 'In Progress' },
    { id: 3, description: 'Voter intimidation reported', category: 'Security', reportedBy: 'Observer C', status: 'Resolved' },
  ]);

  const [newIssue, setNewIssue] = useState({
    description: '',
    category: '',
    reportedBy: '',
  });

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setNewIssue({ ...newIssue, [name]: value });
  };

  const handleReportIssue = () => {
    if (newIssue.description && newIssue.category && newIssue.reportedBy) {
      setIssues([...issues, { ...newIssue, id: issues.length + 1, status: 'Pending' }]);
      setNewIssue({ description: '', category: '', reportedBy: '' }); // Reset input fields
    } else {
      alert('Please fill out all fields');
    }
  };

  const handleResetForm = () => {
    setNewIssue({ description: '', category: '', reportedBy: '' });
  };

  return (
    <Container maxWidth="md">
      {/* Update back button to navigate to Data Analyst page */}
      <Button variant="contained" color="secondary" onClick={() => navigate('/data-analyst')} sx={{ mt: 2 }}>
        Back
      </Button>

      <Box sx={{ my: 4, p: 3, boxShadow: 3, borderRadius: 2, backgroundColor: '#ffffff' }}>
        <Typography variant="h4" align="center" gutterBottom color="primary">
          Polling Station Issues
        </Typography>

        <Typography variant="h6" gutterBottom color="text.primary">
          Reported Issues:
        </Typography>
        {issues.length > 0 ? (
          <Box component="ul">
            {issues.map((issue) => (
              <Box
                component="li"
                key={issue.id}
                sx={{
                  mb: 2,
                  p: 2,
                  borderBottom: '1px solid #ddd',
                  backgroundColor: '#f0f0f0',
                }}
              >
                <Typography variant="body1" sx={{ color: '#000000' }}>
                  <strong>Issue:</strong> {issue.description}
                </Typography>
                <Typography variant="body2" sx={{ color: '#1a73e8' }}>
                  <strong>Category:</strong> {issue.category}
                </Typography>
                <Typography variant="body2" sx={{ color: '#ff5722' }}>
                  <strong>Reported By:</strong> {issue.reportedBy}
                </Typography>
                <Typography variant="body2" sx={{ color: '#4caf50' }}>
                  <strong>Status:</strong> {issue.status}
                </Typography>
              </Box>
            ))}
          </Box>
        ) : (
          <Typography>No issues reported yet.</Typography>
        )}

        {/* Modified Section Heading */}
        <Typography variant="h5" gutterBottom mt={4} color="primary" fontWeight="bold">
          Submit a New Issue to Improve Voting Experience:
        </Typography>
        <Box component="form" onSubmit={(e) => e.preventDefault()}>
          <TextField
            label="Issue Description"
            name="description"
            value={newIssue.description}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
            multiline
            rows={2}
          />
          <FormControl fullWidth margin="normal">
            <InputLabel>Category</InputLabel>
            <Select
              name="category"
              value={newIssue.category}
              onChange={handleInputChange}
            >
              <MenuItem value="Queue Management">Queue Management</MenuItem>
              <MenuItem value="Equipment">Equipment</MenuItem>
              <MenuItem value="Security">Security</MenuItem>
              <MenuItem value="Voter Assistance">Voter Assistance</MenuItem>
            </Select>
          </FormControl>
          <TextField
            label="Reported By"
            name="reportedBy"
            value={newIssue.reportedBy}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              onClick={handleReportIssue}
              sx={{ mt: 2 }}
            >
              Submit Issue
            </Button>
            <Button
              variant="outlined"
              color="warning"
              onClick={handleResetForm}
              sx={{ mt: 2 }}
            >
              Reset Form
            </Button>
          </Box>
        </Box>
      </Box>
    </Container>
  );
};

export default PollingIssues;
