// src/pages/DemographicAnalysis.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js'; // Import necessary components
import './DemographicAnalysis.css'; // Import your CSS file for styling

// Register Chart.js components
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const DemographicAnalysis = () => {
  const navigate = useNavigate();

  // Sample data for demographic analysis visualization
  const demographicData = {
    labels: ['18-24', '25-34', '35-44', '45-54', '55-64', '65+'],
    datasets: [
      {
        label: 'Voter Turnout (%)',
        data: [45, 55, 60, 65, 70, 75], // Example turnout data by age group
        backgroundColor: 'rgba(54, 162, 235, 0.6)',
        borderColor: 'rgba(54, 162, 235, 1)',
        borderWidth: 1,
      },
    ],
  };

  return (
    <div className="report-container">
      <button className="back-button" onClick={() => navigate('/data-analyst')}>Back</button>
      <h2 className="report-title">Demographic Analysis Report</h2>
      <p className="report-summary">
        This report provides insights into voter demographics, highlighting shifts in age groups 
        and the increasing engagement among younger voters (18-24 age group).
        Recommendations include social media campaigns and outreach initiatives to maintain 
        engagement with this demographic.
      </p>

      <div className="chart-container">
        <h3>Voter Turnout by Age Group</h3>
        <Bar data={demographicData} options={{ responsive: true, maintainAspectRatio: false }} />
      </div>

      <div className="key-metrics">
        <h3>Key Metrics</h3>
        <ul>
          <li><strong>Total Voters Registered:</strong> 2,500,000</li>
          <li><strong>Young Voter Turnout (18-24):</strong> 45%</li>
          <li><strong>Turnout Rate for Ages 25-34:</strong> 55%</li>
          <li><strong>Turnout Rate for Ages 35-44:</strong> 60%</li>
        </ul>
      </div>

      <div className="recommendations">
        <h3>Recommendations</h3>
        <ul>
          <li>Develop targeted social media campaigns to engage younger voters.</li>
          <li>Conduct workshops in colleges and universities to educate about the voting process.</li>
          <li>Collaborate with influencers to promote voter registration.</li>
          <li>Utilize text messaging reminders for upcoming elections.</li>
        </ul>
      </div>

      <div className="report-conclusion">
        <h3>Conclusion</h3>
        <p>
          Understanding voter demographics is essential for shaping effective engagement strategies. 
          By focusing on younger voters and implementing innovative outreach methods, we can enhance 
          participation rates and foster a more inclusive electoral process.
        </p>
      </div>
    </div>
  );
};

export default DemographicAnalysis;
