// src/components/Login.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Login.css'; // Import the CSS file
import { getAnalyst } from '../services/api';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleLogin = async(e) => {
    e.preventDefault();
    try {
      const users=await getAnalyst()
      const userExists = users.find(
        (user) => user.username === username && user.password === password
      );
      if (userExists) {
        console.log('Login successful');
        setError('');
        navigate("/data-analyst")
        // Proceed with further actions (e.g., redirecting to a dashboard)
        // navigate('/dashboard'); // Uncomment if you want to navigate after login
      } else {
        console.log('Invalid username or password');
        setError('Invalid username or password'); // Set error message
      }
    } catch (error) {
      console.log(error); // Handle the error
      setError('Something went wrong! Please try again.'); // Set error message
    }
  };

  return (
    <div className="login-container">
      <h2>Login</h2>
      <form onSubmit={handleLogin}>
        <div>
          <label>Username:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Password:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <div>
          <button type="submit">Login</button>
        </div>
      </form>
    </div>
  );
}

export default Login;
