import React, { useEffect, useState } from 'react';
import { getElections} from '../services/apiService';
import './ViewElections.css';

const ViewElectionCitizen = () => {
    const [elections, setElections] = useState([]);
   

    useEffect(() => {
        const fetchElections = async () => {
            try {
                const response = await getElections();
                setElections(response);
            } catch (error) {
                console.error('Error fetching elections:', error);
            }
        };

        fetchElections();
    }, []);

  

   

    

    
    return (
        <div className="view-elections-container">
            <h2>View Elections</h2>
                <ul className="elections-list">
                    {elections.map((election) => (
                        <li key={election.id} className="election-item">
                            <div className="election-details">
                            <h3>{election.name}</h3>
                                        <p>Date: {election.electionDate}</p>
                                        <p>Location: {election.location}</p>
                                        <p>Status: {election.status}</p>
                            </div>
                    
                        </li>
                    ))}
                </ul>
        </div>
    );
};

export default ViewElectionCitizen;
