import React from 'react';
import { useNavigate } from 'react-router-dom';
import './CitizenPage.css';

const CitizenPage = () => {
  const navigate = useNavigate();
  const handleLogout = () => {
    navigate('/access'); // Redirect to the Admin Login page
  };

  const handleViewElections = () => {
    navigate('/citizenViewElection'); // Navigate to the View Elections page
  };

  const handleCheckVoterStatus = () => {
    navigate('/voter-status'); // Navigate to the Voter Status page
  };

  const handleViewVotingHistory = () => {
    navigate('/voting-history'); // Navigate to the Voting History page
  };

  const handleReportIssue = () => {
    navigate('/report-issue'); // Navigate to the Report Issue page
  };

  const handleVotePoll = () => {
    navigate('/poll-vote'); // Navigate to the Poll Vote page
  };

  return (
    <div className="citizen-page-container">
      
      <button className="logout-button" onClick={handleLogout}>
          Logout
        </button>

      <h2>Citizen Dashboard</h2>
      <p className="welcome-message">
        Welcome to the Citizen Dashboard. Here, you can stay informed about upcoming elections, check your voter status, view your voting history, and report any issues you encounter during elections.
      </p>

      <div className="dashboard-sections">
        <div className="dashboard-card" onClick={handleViewElections}>
          <h3>Election Information</h3>
          <p>Stay informed about upcoming elections in your region.</p>
          <button className="action-button">View Elections</button>
        </div>

        <div className="dashboard-card" onClick={handleCheckVoterStatus}>
          <h3>Voter Status</h3>
          <p>Check your voter registration and voting status.</p>
          <button className="action-button">Check Voter Status</button>
        </div>

        <div className="dashboard-card" onClick={handleViewVotingHistory}>
          <h3>Your Voting History</h3>
          <p>Review your past voting records.</p>
          <button className="action-button">View Voting History</button>
        </div>

        <div className="dashboard-card" onClick={handleReportIssue}>
          <h3>Report Election Issues</h3>
          <p>Report any issues you encounter during elections.</p>
          <button className="action-button">Report an Issue</button>
        </div>

        <div className="dashboard-card" onClick={handleVotePoll}>
          <h3>Poll Vote</h3>
          <p>Vote for your preferred party and leader.</p>
          <button className="action-button">Cast Your Vote</button>
        </div>
      </div>

      <div className="footer">
        <h3>Contact Election Helpdesk</h3>
        <p>
          If you need assistance, please contact the election helpdesk at{' '}
          <a href="mailto:helpdesk@example.com">helpdesk@example.com</a>.
        </p>
      </div>
    </div>
  );
};

export default CitizenPage;
