// src/pages/VoterEngagementReport.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js'; // Import necessary components
import './VoterEngagementReport.css';

// Register Chart.js components
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const VoterEngagementReport = () => {
  const navigate = useNavigate();

  // Sample data for voter engagement visualization
  const voterData = {
    labels: ['2018', '2019', '2020', '2021', '2022'],
    datasets: [
      {
        label: 'Voter Turnout (%)',
        data: [60, 65, 70, 75, 85], // Example turnout data
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
      },
    ],
  };

  return (
    <div className="report-container">
      <button className="back-button" onClick={() => navigate('/data-analyst')}>Back</button>
      <h2 className="report-title">Voter Engagement Report</h2>
      <p className="report-summary">
        This report analyzes voter turnout across different demographics and geographic regions. 
        Key findings indicate a 15% increase in participation compared to previous elections. 
        The report suggests targeted outreach efforts for first-time voters and underrepresented communities to further improve engagement.
      </p>

      <div className="chart-container">
        <h3>Voter Turnout Over the Years</h3>
        <Bar data={voterData} options={{ responsive: true, maintainAspectRatio: false }} />
      </div>

      <div className="key-metrics">
        <h3>Key Metrics</h3>
        <ul>
          <li><strong>Total Voters Registered:</strong> 2,500,000</li>
          <li><strong>Total Votes Cast:</strong> 1,875,000</li>
          <li><strong>Turnout Rate:</strong> 75%</li>
          <li><strong>First-Time Voter Turnout:</strong> 35%</li>
        </ul>
      </div>

      <div className="recommendations">
        <h3>Recommendations</h3>
        <ul>
          <li>Enhance outreach programs targeting first-time voters.</li>
          <li>Implement mobile registration units in underrepresented areas.</li>
          <li>Leverage social media campaigns to raise awareness.</li>
          <li>Organize community events to educate voters on the election process.</li>
        </ul>
      </div>

      <div className="report-conclusion">
        <h3>Conclusion</h3>
        <p>
          Engaging voters is crucial for a healthy democracy. Continuous efforts to understand and improve voter engagement will contribute to higher turnout rates and more representative electoral outcomes.
        </p>
      </div>
    </div>
  );
};

export default VoterEngagementReport;
