import React from 'react';
import { useNavigate } from 'react-router-dom';
import './AdminPage.css';

const AdminPage = () => {
  const navigate = useNavigate();

  const handleLogout = () => {
    navigate('/access'); // Redirect to the Admin Login page
  };

  return (
    <div className="admin-page-container">
      <div className="admin-header">
        
        <button className="logout-button" onClick={handleLogout}>
          Logout
        </button>
      </div>

      <h2>Admin Dashboard</h2>
      <p className="welcome-message">
        Welcome to the Admin Dashboard. Here, you can manage users, oversee elections, generate reports, configure system settings, and more.
      </p>

      <div className="dashboard-sections">
        {/* User Management Section */}
        <div className="section-container user-management">
          <h3>User Management</h3>
          <p>Manage users and their roles efficiently.</p>
          <div className="button-group">
            <button className="action-button" onClick={() => navigate('/view-users')}>
              View Users
            </button>
            <button className="action-button" onClick={() => navigate('/add-user')}>
              Add New User
            </button>
          </div>
        </div>

        {/* Election Management Section */}
        <div className="section-container election-management">
          <h3>Election Management</h3>
          <p>Oversee and create elections for a smooth electoral process.</p>
          <div className="button-group">
            <button className="action-button" onClick={() => navigate('/view-elections')}>
              View Elections
            </button>
            <button className="action-button" onClick={() => navigate('/create-election')}>
              Create New Election
            </button>
          </div>
        </div>

        {/* All Voters Section */}
        <div className="section-container voter-management">
          <h3>All Voters</h3>
          <p>Manage and view all registered voters.</p>
          <button className="action-button" onClick={() => navigate('/voters')}>
            View Voters
          </button>
        </div>

        {/* All Parties Section */}
        <div className="section-container party-management">
          <h3>All Parties</h3>
          <p>Manage and view all political parties.</p>
          <button className="action-button" onClick={() => navigate('/view-parties')}>
            View Parties
          </button>
        </div>

        {/* Reporting Section */}
        <div className="section-container reporting">
          <h3>Results</h3>
          <p>Generate comprehensive results on election.</p>
          <button className="action-button" onClick={() => navigate('/view-results')}>
            View Results
          </button>
        </div>

        {/* System Settings Section */}
        <div className="section-container system-settings">
          <h3>System Settings</h3>
          <p>Configure application settings for optimal performance.</p>
          <button className="action-button" onClick={() => navigate('/update-settings')}>
            Update Settings
          </button>
        </div>

        {/* Admin Support Section */}
        <div className="section-container footer">
          <h3>Admin Support</h3>
          <p>
            If you need assistance, please contact support at{' '}
            <a href="mailto:support@example.com">support@example.com</a>.
          </p>
        </div>
      </div>
    </div>
  );
};

export default AdminPage;
