import React from 'react';
import { Box, Button, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const Support = () => {
  const navigate = useNavigate(); // Initialize the useNavigate hook

  return (
    <Box sx={{ p: 4 }}>
      <Button
        variant="contained"
        onClick={() => navigate(-1)}
        sx={{
          mb: 2,
          backgroundColor: '#1976d2',
          color: '#fff',
          border: '1px solid #115293',
          ':hover': {
            backgroundColor: '#115293',
          },
        }}
      >
        Back
      </Button>

      <Typography variant="h2" gutterBottom>
        Support
      </Typography>
      <Typography variant="body1" paragraph>
        Need assistance? Our dedicated support team is here to help. Whether you have questions or encounter issues
        while using our platform, reach out for guidance and support.
      </Typography>
      <Typography variant="body1" paragraph>
        Contact us at: <a href="mailto:support@example.com">support@example.com</a>
      </Typography>

      <Typography variant="h5" gutterBottom>
        Frequently Asked Questions
      </Typography>
      <Typography variant="body2" paragraph>
        - **How can I reset my password?** <br />
        To reset your password, click on the "Forgot Password" link on the login page and follow the instructions.
      </Typography>
      <Typography variant="body2" paragraph>
        - **How do I report an issue?** <br />
        If you encounter any issues, please contact us via the email provided or use the support form on our website.
      </Typography>

      <Typography variant="h5" gutterBottom>
        Support Hours
      </Typography>
      <Typography variant="body2" paragraph>
        Our support team is available from 9 AM to 5 PM (UTC) on weekdays. We strive to respond to all inquiries as quickly as possible.
      </Typography>
    </Box>
  );
};

export default Support;
